body {
  background-color: #fff;
  font-family: "Montserrat", sans-serif !important;
  position: relative; }

.logo-app {
  background-color: #43a3df;
  height: 100vh;
  position: absolute;
  z-index: 100;
  width: 100%; }
  .logo-app .logo-section {
    text-align: center;
    width: 100%;
    height: 50%;
