  .logo-app .loading-section {
    width: 100%;
    height: 50%;
    display: inline-block; }
    .logo-app .loading-section div {
      margin: 0 auto; }
  .logo-app img {
    width: 250px;
    height: 250px;
    bottom: 0px;
    position: absolute;
    margin-left: -125px;
    -webkit-margin-after: -70px;
            margin-block-end: -70px; }

.bubble-link > a {
  color: #000 !important;
  width: calc(100% - 22px);
  word-wrap: break-word; }

.bubble-direction-reverse .bubble-link > a {
  color: #fff !important; }

.notification-container {
  top: 60px; }



